.infoContainer {
  display: flex;
  padding: 5px;
  margin: 5px;
  background-color: #303e53;
  color: white;
  border-radius: 0.5rem;
  justify-content: center;
}

.right,
.left {
  padding: 15px;
}

.right {
  margin-top: 25px;
}

.right h1 {
  margin-bottom: 10px;
}

.pokemonInfoImg {
  margin-top: 20px;
  max-width: 250px;
}

.pokemonInfoImg:hover {
  transform: scale(1.1);
}

.PokemonTypes {
  margin: 10px;
}

link {
  text-decoration: none;
}

span > a {
  text-decoration: none;
  color: black;
}

.pokemonTypesBtn {
  color: white;
}
