* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  background-color: #2d384d;
  text-align: center;
}

.typeLink {
  text-decoration: none;
  text-align: center;
}

.back {
  padding: 10px;
  background-color: green;
  color: white;
  margin-top: 10px;
}

.back:hover {
  color: black;
  background-color: #eee;
}

h3 {
  width: 100%;
  margin: 20px;
}

.navTypes {
  margin-top: 20px;
  height: fit-content;
  padding: 25px;
  background-color: #303e53;
  color: #eee;
}

.navTypes > .navText {
  font-size: 3rem;
}

.pokeTypes {
  height: fit-content;
  color: white;
  padding: 25px;
  background-color: #303e53;
}

.homeImageDiv {
  width: 50%;
  margin: 0 auto;
}

.homeImage {
  width: 100%;
}

.pickachuOutlineDiv {
  width: 20%;
  margin: 0 auto;
}

.pickachuOutline {
  width: 100%;
}
